import { Component, Input, OnInit } from '@angular/core';
import { Carrier411CategoryDTO } from '../../interfaces/carrier411-category.dto';
import {
  faBookMedical,
  faBooksMedical,
  faCommentMedical,
  faCrutches,
  faDisplayMedical,
  faEye,
  faHeartPulse,
  faM,
  faMoneyBillTrendUp,
  faSackDollar,
  faStethoscope,
  faTag,
  faTags,
  faTooth,
  faUsersMedical,
  faWheelchair,
  IconDefinition
} from '@fortawesome/pro-regular-svg-icons';

@Component({
  selector: 'category-icon',
  templateUrl: './category-icon.component.html',
  styleUrls: ['./category-icon.component.scss']
})
export class CategoryIconComponent implements OnInit {
  @Input('category')
  public category: Carrier411CategoryDTO | null = null;

  public icons: { [id: string]: IconDefinition } = {
    'enrollment': faDisplayMedical,
    'aca': faStethoscope,
    'dental': faTooth,
    'vision': faEye,
    'life': faHeartPulse,
    'ltd': faWheelchair,
    'std': faCrutches,
    'discounts': faTags,
    'guidelines': faBooksMedical,
    'contact': faCommentMedical,
    'renewal': faBookMedical,
    'rate status': faSackDollar,
    'bonus': faMoneyBillTrendUp,
    'medicare': faM,
    'large group medical': faUsersMedical,
  };

  constructor() { }

  ngOnInit(): void {
  }

  getCategoryIcon(category: Carrier411CategoryDTO): IconDefinition {
    const categoryIcon = this.filterIcon(category.name);
    if (categoryIcon) return categoryIcon;

    return faTag;
  }

  filterIcon(categoryName: string): IconDefinition | null {
    for (let key in this.icons) {
      if (categoryName.toLowerCase().includes(key.toLowerCase())) {
        return this.icons[key];
      }
    }

    return null;
  }
}
