import { Component, Input, OnChanges, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { DialogComponent, TooltipComponent } from '@syncfusion/ej2-angular-popups';
import { Carrier411CategoryDTO } from 'src/app/carrier411/shared/interfaces/carrier411-category.dto';
import { ItemDTO } from 'src/app/shared/interfaces/item.dto';
import { Carrier411CategoryFiltersComponent } from '../carrier411-category-filters/carrier411-category-filters.component';
import { faSliders } from '@fortawesome/pro-regular-svg-icons';
import { EventArgs } from '@syncfusion/ej2-angular-navigations';

@Component({
  selector: 'carrier411-searchbar',
  templateUrl: './carrier411-searchbar.component.html',
  styleUrls: ['./carrier411-searchbar.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class Carrier411SearchbarComponent implements OnChanges {
  faSliders = faSliders;

  @Input()
  public categories!: Carrier411CategoryDTO[];

  public selectedCategoriesText: string = 'All Categories';
  public selectedCategoriesIds: number[] = [];

  @ViewChild('categoriesToolTip')
  private categoriesToolTip!: TooltipComponent;

  @ViewChild('categoryFilters')
  private categoryFilters!: Carrier411CategoryFiltersComponent;
  @ViewChild('ejCategoryFiltersDialog')
  private categoryFiltersDialog!: DialogComponent;

  constructor() { }

  ngOnChanges(): void {
    this.selectedCategoriesIds = this.categories.map(c => c.id);
  }

  public beforeOpenTooltip(args: EventArgs): void {
    if (this.categoriesToolTip.content == undefined ||
      this.categoriesToolTip.content == null) {
      args.cancel = true;
    }
  }

  public onOpenModal(args: any): void {
    //Preventing the default dialog focus
    args.preventFocus = true;
  }

  public onCloseModal(modal: DialogComponent): void {
    modal.hide();
  }

  public onOverlayClick(): void {
    this.categoryFiltersDialog?.hide();
  }

  public openCategoryFilters(): void {
    this.categoryFilters.loadOptions();
    this.categoryFiltersDialog.show();
  }

  public onUpdateSelectedCategories(categories: ItemDTO[]) {
    this.selectedCategoriesIds = categories.map(c => c.id);
    this.selectedCategoriesText = this.getSelectedCategoryText();
    this.categoryFiltersDialog.hide();
  }

  public getSelectedCategoriesDescription(): string | null {    
    if (this.selectedCategoriesIds && this.selectedCategoriesIds.length &&
      this.selectedCategoriesIds.length != this.categories.length) {
      let content = '<div>'
      content += this.categories.filter(c => this.selectedCategoriesIds.includes(c.id)).map(c => {
        return c.name;
      }).join('<br />');
      content += '</div>';

      return content;
    }

    return '';
  }

  private getSelectedCategoryText(): string {
    if (this.selectedCategoriesIds.length == this.categories.length) {
      return 'All Categories';
    }

    if (this.selectedCategoriesIds.length == 1) {
      return '1 category';
    }

    return `${this.selectedCategoriesIds.length} categories`;
  }
}
