import { HttpClient, HttpParams } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { ServiceResultDTO } from 'src/app/core/interfaces/service-result/service-result.dto';
import { BaseAPIService } from '../../../core/api/base.api.service';
import { Carrier411CategoryDTO } from '../../shared/interfaces/carrier411-category.dto';
import { Carrier411SubCategoryDTO } from '../../shared/interfaces/carrier411-subcategory.dto';
import { Carrier411ApiEndpoints } from '../constants/carrier411-api.constants';

@Injectable({
  providedIn: 'root'
})
export class Carrier411ApiService extends BaseAPIService {

  constructor(
    @Inject("BASE_API_URL_WEB") baseApiUrl_Web: string, http: HttpClient
  ) {
    super(baseApiUrl_Web, http);
  }

  /* Categories */
  public getCategories() {
    return this.http.get<ServiceResultDTO<Carrier411CategoryDTO[]>>(
      this.constructRequestUrl(Carrier411ApiEndpoints.GetCategories())
    );
  }

  /* SubCategories */
  public getSubCategories(categoryIds: number[]) {
    let params = new HttpParams();
    categoryIds.forEach(c => {
      params = params.append('categoryIds', c);
    });

    return this.http.get<ServiceResultDTO<Carrier411SubCategoryDTO[]>>(
      this.constructRequestUrl(Carrier411ApiEndpoints.GetSubCategories()), { params }
    );
  }
}
