import { Component, EventEmitter, Input, OnChanges, OnInit, Output } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { faChevronDown, faChevronRight, faSliders } from '@fortawesome/pro-regular-svg-icons';
import { Carrier411CategoryDTO } from 'src/app/carrier411/shared/interfaces/carrier411-category.dto';
import { Carrier411SubCategoryDTO } from 'src/app/carrier411/shared/interfaces/carrier411-subcategory.dto';
import { ApplicationUrls } from 'src/app/shared/constants/application-urls';
import { ItemDTO } from 'src/app/shared/interfaces/item.dto';
import { Carrier411CategoryNavbarDTO } from '../../interfaces/carrier411-category-navbar.dto';

@Component({
  selector: 'carrier411-navbar',
  templateUrl: './carrier411-navbar.component.html',
  styleUrls: ['./carrier411-navbar.component.scss']
})
export class Carrier411NavbarComponent implements OnInit, OnChanges {
  Carrier411URL = ApplicationUrls.Carrier411.path;

  faChevronDown = faChevronDown;
  faChevronRight = faChevronRight;
  faSliders = faSliders;

  @Input()
  public expanded: boolean = true;
  @Input()
  public categories: Carrier411CategoryDTO[] = [];
  @Input()
  public subCategories: Carrier411SubCategoryDTO[] = [];

  @Output('onExpandMenu')
  public onExpandMenuTrigger = new EventEmitter();
  @Output('onExpandCategory')
  public onExpandCategoryTrigger = new EventEmitter<number>();
  @Output('onSubCategoryChange')
  public onSubCategoryChangeTrigger = new EventEmitter<number[]>();

  public items: Carrier411CategoryNavbarDTO[] = [];
  private categoryIds: number[] = [];

  constructor(
    private route: ActivatedRoute
  ) { }

  ngOnInit(): void {
    this.route.queryParams.subscribe(params => {
      this.categoryIds = params['categoryIds'].toString().split(',').map((c: string) => Number(c));
      this.setupItems();
    });
  }

  ngOnChanges(): void {
    if (this.items && this.items.length) {
      this.items
        .filter(c => c.subCategories == null || c.subCategories.length == 0)
        .forEach(c => {
          c.subCategories = this.filterSubCategories(c.id);
        });
    }
    else {
      this.items = this.categories.map(c => {
        return {
          ...c,
          expanded: this.categoryIds.includes(c.id),
          subCategories: this.filterSubCategories(c.id)
        };
      });
    }
  }

  public onExpand(): void {
    this.onExpandMenuTrigger.emit();
  }

  public toggleCategory(category: Carrier411CategoryNavbarDTO): void {
    category.expanded = !category.expanded;
    if (category.expanded) {
      this.onExpandCategoryTrigger.emit(category.id);
    }
  }

  public selectAll(category: Carrier411CategoryNavbarDTO): void {
    let changed = false;
    category.subCategories.filter(sc => !sc.selected).forEach((sc: any) => {
      changed = true;
      sc.selected = true;
    });

    if (changed) {
      this.onSubCategoryChange();
    }
  }

  public clear(category: Carrier411CategoryNavbarDTO): void {
    let changed = false;
    category.subCategories.filter(sc => sc.selected).forEach((sc: any) => {
      changed = true;
      sc.selected = false;
    });

    if (changed) {
      this.onSubCategoryChange();
    }
  }

  public onSubCategoryChange(): void {
    const subCategoryIds = this.items.flatMap(c => c.subCategories.filter(sc => sc.selected).map(sc => sc.id));
    this.onSubCategoryChangeTrigger.emit(subCategoryIds);
  }

  private filterSubCategories(categoryId: number): ItemDTO[] {
    return this.subCategories
      .filter(sc => sc.category.id == categoryId)
      .sort(sc => sc.order)
      .map(sc => {
        return {
          id: sc.id,
          name: sc.name,
          selected: false
        }
      })
  }

  private setupItems(): void {
    this.items.filter(i => this.categoryIds.includes(i.id)).forEach(i => {
      i.expanded = true;
    });
  }
}
