import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { ResultHandlerService } from 'src/app/core/services/result.handler.service';
import { faChevronRight, faMegaphone } from '@fortawesome/pro-regular-svg-icons';
import { ToolMessagesDTO } from 'src/app/shared/interfaces/tools/tool-messages.dto';
import { ActivatedRoute } from '@angular/router';
import { ServiceResultDTO } from 'src/app/core/interfaces/service-result/service-result.dto';
import { Carrier411CategoryDTO } from 'src/app/carrier411/shared/interfaces/carrier411-category.dto';
import { ApplicationUrls } from 'src/app/shared/constants/application-urls';
import { ToolsList } from 'src/app/shared/constants/tools-list.enum';

@Component({
  selector: 'app-carrier411-landing',
  templateUrl: './carrier411-landing.component.html',
  styleUrls: ['./carrier411-landing.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class Carrier411LandingComponent implements OnInit {
  Carrier411Id = ToolsList.Carrier411;

  BrowseCarrier411Url = ApplicationUrls.Carrier411.Browse.path;
  faChevronRight = faChevronRight;
  faMegaphone = faMegaphone;

  public toolMessages!: ToolMessagesDTO;
  public categories!: Carrier411CategoryDTO[];

  constructor(
    private route: ActivatedRoute,
    private resultHandlerService: ResultHandlerService
  ) {
    this.toolMessages = this.handleResolverData<ToolMessagesDTO>('messages');
    this.categories = this.handleResolverData<Carrier411CategoryDTO[]>('categories');
  }

  ngOnInit(): void {
  }

  private handleResolverData<T>(key: string): any | null {
    const data = this.route.snapshot.data[key] as ServiceResultDTO<T>;
    if (data.isSuccess) {
      return data.value;
    } else {
      this.resultHandlerService.handle(data);
      return null;
    }
  }
}
