import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { ServiceResultDTO } from 'src/app/core/interfaces/service-result/service-result.dto';
import { Carrier411CategoryDTO } from '../../shared/interfaces/carrier411-category.dto';
import { Carrier411SubCategoryDTO } from '../../shared/interfaces/carrier411-subcategory.dto';
import { Carrier411ApiService } from './carrier411.api.service';

@Injectable({
  providedIn: 'root'
})
export class Carrier411Service {

  constructor(private carrier411ApiService: Carrier411ApiService) { }

  /* Categories */
  public getCategories(): Observable<ServiceResultDTO<Carrier411CategoryDTO[]>> {
    return this.carrier411ApiService.getCategories();
  }

  /* SubCategories */
  public getSubCategories(categoryIds: number[]): Observable<ServiceResultDTO<Carrier411SubCategoryDTO[]>> {
    return this.carrier411ApiService.getSubCategories(categoryIds);
  }
}
