import { Injectable } from '@angular/core';
import { Resolve, RouterStateSnapshot, ActivatedRouteSnapshot, Router } from '@angular/router';
import { Observable } from 'rxjs';
import { ServiceResultDTO } from '../../../core/interfaces/service-result/service-result.dto';
import { Carrier411CategoryDTO } from '../../shared/interfaces/carrier411-category.dto';
import { Carrier411Service } from '../services/carrier411.service';

@Injectable()
export class CategoriesListResolver implements Resolve<ServiceResultDTO<Carrier411CategoryDTO[]>> {

  constructor(
    private router: Router,
    private carrier411Service: Carrier411Service
  ) {
  }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<ServiceResultDTO<Carrier411CategoryDTO[]>> {
    const stateData = this.router?.getCurrentNavigation()?.extras.state;

    const hasArrayInHistoryState = stateData != null &&
      typeof stateData === 'object' &&
      stateData.categories != null &&
      stateData.categories instanceof Array;

    if (hasArrayInHistoryState) {
      return {
        isSuccess: true,
        value: stateData.categories
      } as any;
    }

    return this.carrier411Service.getCategories();
  }
}
