import { Injectable } from '@angular/core';
import { Resolve, RouterStateSnapshot, ActivatedRouteSnapshot } from '@angular/router';
import { Observable } from 'rxjs';
import { ServiceResultDTO } from '../../../core/interfaces/service-result/service-result.dto';
import { Carrier411SubCategoryDTO } from '../../shared/interfaces/carrier411-subcategory.dto';
import { Carrier411Service } from '../services/carrier411.service';

@Injectable()
export class SubCategoriesListResolver implements Resolve<ServiceResultDTO<Carrier411SubCategoryDTO[]>> {

  constructor(
    private carrier411Service: Carrier411Service
  ) {
  }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<ServiceResultDTO<Carrier411SubCategoryDTO[]>> {
    const categoryIds = route.queryParamMap.get('categoryIds')?.split('.').map(c => Number(c)) ?? [];
    return this.carrier411Service.getSubCategories(categoryIds);
  }
}
