import { Component, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Carrier411CategoryDTO } from 'src/app/carrier411/shared/interfaces/carrier411-category.dto';
import { Carrier411SubCategoryDTO } from 'src/app/carrier411/shared/interfaces/carrier411-subcategory.dto';
import { ServiceResultDTO } from 'src/app/core/interfaces/service-result/service-result.dto';
import { ResultHandlerService } from 'src/app/core/services/result.handler.service';
import { ToolsList } from 'src/app/shared/constants/tools-list.enum';
import { Carrier411Service } from '../../services/carrier411.service';
import { faMagnifyingGlass } from '@fortawesome/pro-regular-svg-icons';
import { ApplicationUrls } from 'src/app/shared/constants/application-urls';
import { SplitterComponent } from '@syncfusion/ej2-angular-layouts';

@Component({
  selector: 'app-carrier411-browse',
  templateUrl: './carrier411-browse.component.html',
  styleUrls: ['./carrier411-browse.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class Carrier411BrowseComponent implements OnInit {
  Carrier411Id = ToolsList.Carrier411;
  faMagnifyingGlass = faMagnifyingGlass;
  carrier411Url = ApplicationUrls.Carrier411.path;

  public categories: Carrier411CategoryDTO[] = [];
  public subCategories: Carrier411SubCategoryDTO[] = [];

  public selectedSubCategoryIds: number[] = [];

  public menuExpanded: boolean = true;

  @ViewChild('ejsSplitter')
  private ejsSplitter!: SplitterComponent;

  constructor(
    private route: ActivatedRoute,
    private resultHandlerService: ResultHandlerService,
    private carrier411Service: Carrier411Service
  ) {
    this.categories = this.handleResolverData<Carrier411CategoryDTO>('categories');
    this.subCategories = this.handleResolverData<Carrier411SubCategoryDTO>('subCategories');
  }

  ngOnInit(): void {
  }

  public onExpandMenu(): void {
    this.ejsSplitter.expand(0);
  }

  public onExpandCategory(categoryId: number) {
    if (!this.subCategories.some(sc => sc.category.id == categoryId)) {
      this.carrier411Service.getSubCategories([categoryId]).subscribe(data => {
        if (data.isSuccess) {
          this.subCategories = this.subCategories.concat(data.value).sort(sc => sc.order);
        } else {
          this.resultHandlerService.handle(data);
        }
      });
    }
  }

  public onSubCategoryChange(subCategoryIds: number[]) {
    this.selectedSubCategoryIds = subCategoryIds;
    // TODO
  }

  public toggleMenu() {
    this.menuExpanded = !this.menuExpanded;
  }

  private handleResolverData<T>(key: string): T[] {
    const data = this.route.snapshot.data[key] as ServiceResultDTO<T[]>;
    if (data) {
      if (data.isSuccess) {
        return data.value;
      } else {
        this.resultHandlerService.handle(data);
        return [];
      }
    } else {
      return [];
    }
  }
}
