import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

// Components
import { Carrier411BrowseComponent } from './components/carrier411-browse/carrier411-browse.component';
import { Carrier411LandingComponent } from './components/carrier411-landing/carrier411-landing.component';

// Resolvers
import { Carrier411MessagesResolver } from '../shared/resolvers/carrier411-messages.resolver';
import { CategoriesListResolver } from './resolvers/categories-list.resolver';
import { SubCategoriesListResolver } from './resolvers/sub-categories-list.resolver';

const childRoutes: Routes = [
  {
    path: '',
    component: Carrier411LandingComponent,
    resolve: { messages: Carrier411MessagesResolver, categories: CategoriesListResolver }
  },
  {
    path: 'browse',
    component: Carrier411BrowseComponent,
    resolve: { categories: CategoriesListResolver, subCategories: SubCategoriesListResolver }
  },
]

@NgModule({
  declarations: [],
  imports: [RouterModule.forChild(childRoutes)],
  exports: [RouterModule]
})

export class Carrier411RoutingModule { }
