import { Component, EventEmitter, Input, OnChanges, OnInit, Output } from '@angular/core';
import { Carrier411CategoryDTO } from 'src/app/carrier411/shared/interfaces/carrier411-category.dto';
import { ItemDTO } from 'src/app/shared/interfaces/item.dto';
import { ToastService } from 'src/app/shared/services/toast.service';

@Component({
  selector: 'carrier411-category-filters',
  templateUrl: './carrier411-category-filters.component.html',
  styleUrls: ['./carrier411-category-filters.component.scss']
})
export class Carrier411CategoryFiltersComponent implements OnChanges {
  @Input()
  public categories: Carrier411CategoryDTO[] = [];
  @Input()
  public selectedIds: number[] | undefined = [];

  @Output('onUpdate')
  public onUpdateTrigger = new EventEmitter<ItemDTO[]>();
  @Output('onClose')
  public onCloseTrigger = new EventEmitter();

  public options?: ItemDTO[] = undefined;

  constructor(private toastService: ToastService) { }

  ngOnChanges(): void {
    this.loadOptions();
  }

  loadOptions(): void {
    this.options = this.categories.map(c => {
      return {
        id: c.id,
        name: c.name,
        selected: this.selectedIds != null && this.selectedIds.findIndex(s => s == c.id) != -1
      }
    });
  }

  selectAll(): void {
    this.options = this.options?.map(o => {
      return {
        ...o,
        selected: true
      };
    });
  }

  clear(): void {
    this.options = this.options?.map(o => {
      return {
        ...o,
        selected: false
      };
    });
  }

  onUpdate(): void {
    const optionsSelected = this.options?.filter(c => c.selected);
    if (optionsSelected?.length == 0) {
      this.toastService.show({
        content: 'At least one category must be selected!',
        icon: 'error'
      });
    } else {
      this.onUpdateTrigger.emit(optionsSelected);
    }

  }

  onClose(): void {
    this.onCloseTrigger.emit();
  }
}
